.alignright {
  float: right;
}

.boxBody {
  width: 811px;
  height: 313px;
  padding: 29px 31px 32px 30px;
}
.new {
  width: 240px;
  height: 252px;
  background-color: #fbfbfd;
  position: relative;
  padding: 16px 23px 51px 25px;
  border: 1px solid #e1e3ea;
  float: left;
}
.detail {
  width: 510px;
  height: 252px;
  border: 1px solid #e1e3ea;
  padding: 35px 26px;
  float: right;
  border-left: none;
}

.new p,
.textbox p {
  margin: 0px;
  padding: 0px;
}

.new p:nth-child(2) {
  font-size: 10px;
  color: #94c1e6;
}
.new p:nth-child(3) {
  font-size: 18px;
  color: #006fbf;
  font-weight: medium;
}
.fa-power-off {
  color: #fcb800;
  font-size: 12px;
}

.new p:nth-child(4) {
  font-size: 12px;
  color: #000000de;
}
.new p:nth-child(5) {
  text-align: center;
  font-size: 12px;
  font-style: italic;
  letter-spacing: 0px;
  color: #191919;
  opacity: 1;
}

.new button {
  background: #006fbf;
  border: 1px solid #e1e3eb;
  width: 121px;
  color: #ffffff;
  font-size: 12px;
  height: 36px;
}

.pass {
  position: absolute;
  bottom: 0px;
  left: 0px;
}
.short {
  position: absolute;
  bottom: 0px;
  right: 0px;
}

.text-blue {
  color: #0072bc;
}
.social {
  color: #006fbf;
  margin-top: 5px !important;
}
.show {
  font-size: 12px;
  letter-spacing: 0px;
  color: #757575;
  opacity: 1;
}
.showtitle,
.text-blue {
  font-size: 14px;
}
.showtitle,
.year,
.text-blue,
.social {
  font-weight: bold;
}
.year,
.social {
  font-size: 12px;
}
.clear {
  clear: both;
}
.bloque {
  margin-bottom: 1px;
  padding-bottom: 2px;
  border-bottom: 1px solid #e1e3ea;
}

.plusCon {
  background-color: #0072bc;
  border: none;
  border-radius: 25px;
  color: #fff;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (orientation: portrait) {
  /*Styles */
}

@media only screen and (max-width: 320px) {
  .container {
    padding: 0%;
  }
  .boxBody {
    width: 100%;
    padding: 0 30px;
  }
  .new {
    width: auto;
    height: 252px;
    background-color: #fbfbfd;
    position: relative;
    padding: 16px 23px 51px 25px;
    border: 1px solid #e1e3ea;
  }
  .detail {
    width: auto;
    height: auto;
    border: 1px solid #e1e3ea;
    padding: 15px;
    border-top: none;
  }
  .showtitle,
  .text-blue,
  .year,
  .show,
  .social {
    font-size: 9px;
    float: left;
    clear: both;
  }

  .bloque {
    text-align: left;
  }
}
